html{
    font-size: 20px;
}
* {
    box-sizing: border-box;
}
html,body{
    width: 100%;
    height: 100%;
}
.g-wrapper {
    position: relative;
    width: 100%;
    min-height: 100%;
    overflow-x: hidden;
    line-height: 1;
    font-size: .7rem;
    font-family: Helvetica,"STHeiti STXihei","Microsoft JhengHei","Microsoft YaHei",Tohoma,Arial;
    -webkit-font-smoothing: inherit;
    color: #515151;
    background-color: #f0f0f0;
}

.g-wrapper {
    header {
        height: 2.3rem;
        background-color: #fff;
        z-index: 0;
        position: relative;
        border-bottom: 1px solid #f0f0f0;
        
        * {
            box-sizing: content-box;
        }
        
        .back_level {
            width: 1.5rem;
            height: 2.3rem;
            line-height: 2.3rem;
            line-height: 1;
            display: inline-block;
            text-align: center;
            position: absolute;
            left: 0;
            top: 0;
            padding-left: .25rem;
            
            .lineae_u, .lineae_d {
                font-size: 0;
                line-height: 0;
                height: .1rem;
                width: .75rem;
                background-color: #515151;
                display: inline-block;
            }
            
            .lineae_u {
                transform: translate(.25rem,.9rem) rotate(45deg);
            }
            
            .lineae_d {
                transform: translate(-.5rem,.4rem) rotate(-45deg);
            }
        }
        
        .headedText {
            text-align: center;
            color: #3a90f4;
            line-height: 2.3rem;
        }
        
        .refreshbt {
            position: absolute;
            width: 1.5rem;
            height: 2.3rem;
            right: 0;
            top: 0;
            background: url(../img/refreshbt.png) no-repeat 0 center;
            background-size: 1.2rem 1.2rem;
            text-indent: -9999px;
        }
    }
}

.indexdope {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#69acfa), color-stop(100%,#398bee));
    padding: 0 0 0 .5rem;
    display: -webkit-box;
    
    * {
        box-sizing: content-box;
    }
    
    .dopetip {
        background: url(../img/dopetip.png) no-repeat 0 center;
        -webkit-background-size: .6rem .6rem;
        background-size: .6rem .6rem;
        padding: .5rem 0 .5rem 0;
        font-size: 0;
        height: 1.3rem;
        line-height: 0;
        width: 1.1rem;
        display: inline-block;
    }
    
    div.dopetext {
        -webkit-box-flex: 1;
        height: 1.3rem;
        overflow: hidden;
        line-height: 1.3rem;
        color: #fff;
        padding: .5rem 0;
        
        span {
            margin-right: .5rem;
        }
    }
    
    .close_level {
        width: 1rem;
        height: 1.25rem;
        line-height: 1;
        display: inline-block;
        text-align: center;
        padding: .5rem;
        
        .close_u, .close_d {
            font-size: 0;
            line-height: 0;
            height: .1rem;
            width: .9rem;
            background-color: #fff;
            display: inline-block;
        }
        
        .close_u {
            transform: translate(.15rem,.15rem) rotate(45deg);
        }
        
        .close_d {
            transform: translate(.15rem,-.55rem) rotate(-45deg);
        }
    }
}

.loading_initial {
    width: 100px;
    height: 100px;
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    left: 50%;
    top: 50%;
    margin: -50px 0 0 -50px;
    display: inline-block;
    -webkit-border-radius: 5px;
    border-radius: 5px;
    text-align: center;
    z-index: 9999;
    
    .load_con {
        width: 40px;
        height: 40px;
        margin: 18px 0 12px 0;
        display: inline-block;
        position: relative;
        z-index: 998;
        
        .load_radius {
            background: url("data:image/png;base64,") no-repeat center center transparent;
            -webkit-background-size: 40px 40px;
            background-size: 40px 40px;
            width: 40px;
            height: 40px;
            display: inline-block;
            left: 0;
            position: absolute;
            z-index: 999;
            -webkit-animation: rotate_load 1.2s linear infinite;
        }
    }
    
    .load_text {
        font-size: 12px;
        color: #fff;
    }
}
@-webkit-keyframes 'rotate_load'{
    from{-webkit-transform: rotate(0deg)}
    to{-webkit-transform: rotate(359deg)};
}

.jconfirm-scrollpane {
    .jconfirm-buttons .btn-large {
        width: 7.4rem;
        height: 2.3rem;
    }
    .jconfirm-title {
        display: block !important;
        margin: 0 auto;
        
        .notice-icon {
            display: block;
            margin: .5rem auto;
            width: 2.4rem;
            height: 2.4rem;
            background: url(../img/notice.png) 0 0 no-repeat;
            background-size: contain;
        }
    }
    
    .jconfirm-content-pane {
        text-align: center;
        font-size: .7rem;
        color: #aaa;
        margin-bottom: 1.5rem !important;
    }
} 

